export const data = `The Pokdex (Japanese:  Pokemon Zukan, lit. "Pokmon Picture Book") is an electronic device designed to catalogue and provide information regarding the various species of Pokmon featured in the Pokmon video game, anime and manga series.

The name Pokdex is a neologism including "Pokmon" (which itself is a portmanteau of "pocket" and "monster") and "index".

The Japanese name "Pokmon Picture Book" can also be translated as "Pokmon Encyclopedia".

However, this way of writing "encyclopedia" would be more accurately translated as "fact book", which is intended to imply that it is intentionally made simplistic and easy for everyone, including children, to understand.

The Pokdex features every Pokmon up to the current generation in its respective game.`;

